        {
          name: '轉帳申請',
          status: 0
        },
        {
          name: '易call保',
          status: 0
        },
        {
          name: '保單貸款',
          status: 0
        },
        {
          name: '理賠抵繳',
          status: 0
        }
      ],
      group: [],
      roleOpt: ['戶長', '配偶', '子女', '其他']
    }
  },
  components: { GroupMemberModal, FamilyModal, WarningModal, AddFamilyModal, Level, Avatar },
  methods: {
    ...mapActions([
      'setPageLock'
    ]),
    
    removeMember(i, data) {
      this.currentDelIndex = i
      this.currentWarningType = 'delFamily'
      this.warningData[this.currentWarningType].message = `您是否確認要刪除家戶成員 <a class="link text-underline">${data.name}<a/> ，若確定刪除，則<a class="link text-underline">${data.name}<a/> 需重新歸戶。`
      this.panelOpen('warningModalOpen')
    },
    warningSubmit () {
      let fn = this.warningData[this.currentWarningType].submit
      this[fn]()
      this.panelClose('warningModalOpen')
    },
    delFamily() {
      this.customer.family.splice(this.currentDelIndex, 1)
    },
    initFamily() {
      this.recordOrResetFamily()
      this.familyEditing = true
      
      let item = [{
        link: 1,
        name: this.customer.info.name,
        gender: 0,
        identity: 'A223456789',
        role: '戶長',
        birthday: '19600809',
        currentCustomer: true
      }]
      this.customer.info.familyLeader = true
      this.customer.family = item
    },
    submitAddFamily (item) {
      this.customer.family = this.customer.family.concat(item)
      this.panelClose('addFamilyModalOpen')
    },
    submitFamily(f) {
      // add family item from api
      this.recordOrResetFamily()
      this.familyEditing = true
      let item = [
        {
          link: 1,
          name: '陳怡君',
          gender: 0,
          identity: 'A223456789',
          role: '戶長',
          birthday: '19600809'
        },
        {
          name: '王大進',
          gender: 1,
          identity: 'A223456789',
          role: '配偶',
          birthday: '19600809'
        },
        {
          name: '王潔心',
          gender: 0,
          identity: 'A223456789',
          role: '子女',
          birthday: '19600809'
        },
        {
          name: '簡沛文',
          gender: -1,
          identity: 'A223456789',
          role: '其他',
          birthday: '19600809'
        },
        {
          link: 1,
          name: '陳潔妮',
          gender: -1,
          identity: 'A223456789',
          role: '其他',
          birthday: '19600809'
        },
        {
          name: '端木王大進',
          gender: -1,
          identity: 'A223456789',
          role: '其他',
          birthday: '19600809'
        }
      ]

      item.push({
        link: 1,
        name: this.customer.info.name,
        gender: 0,
        identity: 'A223456789',
        role: '其他',
        birthday: '19600809',
        currentCustomer: true
      })
      this.customer.info.familyLeader = false
      this.customer.family = item
      this.panelClose('familyModalOpen')
    },
    beforeClearFamily () {
      this.currentWarningType = 'clearFamily'
      this.warningData.clearFamily.message = `您是否確認要刪除以 <b>${customer.info.name}</b> 為戶長之家戶，若確定刪除，則原家戶成員皆須重新歸戶。`
      this.panelOpen('warningModalOpen')
    },
    clearFamily () {
      this.recordOrResetFamily()
      this.familyEditing = true
      this.customer.family = []
      this.customer.info.familyLeader = false
      this.panelClose('warningModalOpen')
    },
    recordOrResetFamily (reset) {
      if(reset) {
        this.customer.info.familyLeader = this.oldFamily.leader
        this.customer.family = this.oldFamily.family
      } else {
        this.oldFamily = {
          leader: window._.clone(this.customer.info.familyLeader),
          family: window._.clone(this.customer.family)
        }
      }
    },
    cancelFamilyEdit () {
      this.recordOrResetFamily(true)
      this.familyEditing = false
    },
    becomeLeader () {
      this.panelClose('warningModalOpen')
      this.familyEditing = false
    },
    submitFamilyEdit () {
      if (!this.oldFamily.leader && this.customer.info.familyLeader) {
        this.warningData.becomeLeader.message = `是否確認要修改<b>${this.customer.info.name}</b> 的家戶關係？`
        this.currentWarningType = 'becomeLeader'
        this.panelOpen('warningModalOpen')
      } else {
        this.familyEditing = false
      }
    },
    editFamily () {
      this.recordOrResetFamily()
      this.familyEditing = true
    },
    selectGroup(item) {
      this.selectedGroup = item
      this.panelOpen('groupModalOpen')
    }
  },async mounted () {
    await getCustomers(this.$route.params.id).then(r => {
      this.customer.info = r[0]
    })
  }
}
</script>
